}
.w_download-wrapper .download-header .effect_picture p {
  position: absolute;
  top: 352px;
  left: 50%;
  -webkit-transform: translate(-50%);
      -ms-transform: translate(-50%);
          transform: translate(-50%);
  width: 180px;
  height: 26px;
  line-height: 26px;
  text-align: center;
  font-size: 14px;
