}
.w_download-wrapper .download-header .effect_picture .download-btn {
  position: absolute;
  top: 479px;
  left: 50%;
  -webkit-transform: translate(-50%);
      -ms-transform: translate(-50%);
          transform: translate(-50%);
  width: 152px;
  height: 42px;
  line-height: 42px;
  border-radius: 30px;
  background: rgba(134, 171, 255, 0.4);
  border: 2px solid #ffffff;
  box-shadow: 0 0 10px 0 rgba(134, 171, 255, 0.7), inset 0 0 3px 0 rgba(27, 101, 172, 0.6);
  -webkit-animation: changeOpacity 4s infinite linear;
          animation: changeOpacity 4s infinite linear;
  z-index: 11;
  cursor: pointer;
}
.w_download-wrapper .download-header .effect_picture .download-btn i {
  position: absolute;
  top: 10px;
  left: 18px;
  width: 26px;
  height: 26px;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: contain;
}
.w_download-wrapper .download-header .effect_picture .download-btn span {
  margin-left: 48px;
  font-size: 20px;
  color: #ffffff;
}
.w_download-wrapper .download-header .effect_picture .circle1,
.w_download-wrapper .download-header .effect_picture .circle2,
.w_download-wrapper .download-header .effect_picture .circle3 {
  position: absolute;
  top: 45px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 720px;
  height: 720px;
  z-index: 9;
}
.w_download-wrapper .download-header .effect_picture .circle1 {
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: contain;
  -webkit-animation: circleRotate 3s infinite linear;
          animation: circleRotate 3s infinite linear;
}
.w_download-wrapper .download-header .effect_picture .circle2 {
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: contain;
  -webkit-animation: circle2Rotate 5s infinite linear;
          animation: circle2Rotate 5s infinite linear;
}
.w_download-wrapper .download-header .effect_picture .circle3 {
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: contain;
  -webkit-animation: circleRotate 8s infinite linear;
          animation: circleRotate 8s infinite linear;
}
.w_download-wrapper .download-header .effect_picture .switch-tab {
  position: absolute;
  bottom: 18px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 238px;
  height: 78px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  z-index: 10;
}
.w_download-wrapper .download-header .effect_picture .switch-tab .pc,
.w_download-wrapper .download-header .effect_picture .switch-tab .phone {
  display: inline-block;
  width: 80px;
  height: 78px;
  border-bottom: 2px solid transparent;
