}
.w_download-wrapper .download-header .effect_picture .switch-tab .activePc,
.w_download-wrapper .download-header .effect_picture .switch-tab .activePhone {
  border-bottom: 2px solid #fff;
  -webkit-animation: changeOpacity 4s infinite linear;
          animation: changeOpacity 4s infinite linear;
}
